.movies{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;

}
.movies .movie{
    display: flex;
    width: 45%;
    background: white;
    align-items: flex-start;
    margin-bottom: 70px;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #4d4d4d;

}

.movie img{
    position: relative;
    max-width: 200px;
    top: -50px;
    margin-right: 30px;
}

.movie .movie_title,
.movie .movie_year{
    font-weight: 300;
    margin: 0;
}

.movie .movie_column{
    width: 80%;
}
.movie .movie_title{
    margin-bottom:  5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie_genres{
    list-style:  none;
    padding: 0;
    margin: 5px 0;
    display: flex;
    flex-wrap: wrap;
}

.movie_genres li,
.movie .movie_year{
    font-size: 14px;
    margin-right: 10px;
}
.movie{
    transition: all 0.5s;
}
.movie:hover{
    background: #fcd700;
    transition: all 0.5s;
}