.navbar{
    height: 100px;
    width: 100%;
    background: grey;
    display: flex;
    justify-content: space-between;
    
    align-items: center;
}



.navbar_list{
    display: flex;
}
.content{
    width: 1200px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
}

.navbar_list li{
    list-style: none;
    padding: 0 0 6px;
    margin: 0 10px;
    font-size: 25px;
    font-weight: 300;
    transition: all 0.5s;
    color: white;
    cursor: pointer;
    border-bottom: 1px solid transparent;

}

.navbar_list li:hover{
    color: #ffd900;
    padding-bottom: 4px;
    border-bottom: 1px solid #ffd900;
    transition: all 0.5s;
    
}